.block {
  border: 1px solid #c9c9c9;
  background-color: rgba(144, 144, 144, 0.075);
  margin-bottom: 20px;
}
.block-label {
  background-color: #24a0de;
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  padding: 0 30px;
}
.voice-block {
  justify-content: space-between;
  display: flex;
}
.voice-block-img {
  text-align: center;
}
.voice-block-img img {
  width: 200px;
}
.voice-block-wav {
  padding-top: 100px;
}
