.main-inner {
  width: 100%;
  max-width: 80em;
  margin: 4em auto;
  padding: 0 2.5em;

}

.title {
  font-size: 48px;
  font-family: sans-serif;
  font-weight: bold;
}

.speech-label {
  color: #24a0de;
  font-weight: 900;
  padding: 0 30px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
